import { initiateNotifier } from './notifier'; //    
import Anime from '../partials/anime'; //    
import initTootTip from '../partials/tooltip'; //    
import AppHelpers from "./app-helpers"; //    
import '../pages/dark-mode';

class App extends AppHelpers {
  constructor() {
    super(); //     
    window.app = this; //     
    this.checkSystemTheme(); //      
  }

  loadTheApp() {
    this.commonThings(); //   
    initiateNotifier(); //  
    this.initAppFeatures(); //   
    this.log('Theme Loaded '); //     
  }

  checkSystemTheme() {
    const prefersDarkScheme = window.matchMedia("(prefers-color-scheme: dark)").matches;
    if (prefersDarkScheme) {
      document.body.classList.add('dark-mode');
      document.body.classList.remove('light-mode');
    } else {
      document.body.classList.add('light-mode');
      document.body.classList.remove('dark-mode');
    }
  }

  initAppFeatures() {
    const features = [
      this.initiateSidebar,
      this.initAddToCart,
      this.initiateAdAlert,
      this.initiateDropdowns,
      this.initiateModals,
      this.initiateCollapse,
      initTootTip,
      this.loadModalImgOnclick
    ];
    
    features.forEach(feature => feature.call(this)); //   
  }

  log(message) {
    salla.log(`ThemeApp(Raed)::${message}`); //     
    return this; //   
  }

  //    
  initiateSidebar() {
    const sidebar = document.getElementById('sidebar-menu'); //     
    if (!sidebar) return; //         

    const overlay = this.createOverlay(); //  
    document.body.appendChild(overlay); //    

    const toggleSidebar = (show = true) => {
      sidebar.classList.toggle('open', show);
    };

    this.setupToggleButtons(toggleSidebar);
    overlay.addEventListener('click', () => toggleSidebar(false));
    this.setupEscapeKey(toggleSidebar);
    this.initiateSidebarSubmenus(sidebar);
    this.setupCloseButton(toggleSidebar);
  }

  setupToggleButtons(toggleSidebar) {
    const toggleBtns = document.querySelectorAll('.menu-trigger'); //    
    toggleBtns.forEach(btn => {
      btn.addEventListener('click', (e) => {
        e.preventDefault();
        const isOpen = document.querySelector('.sidebar-menu').classList.contains('open'); //      
        toggleSidebar(!isOpen); //   
      });
    });
  }

  setupEscapeKey(toggleSidebar) {
    document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape' && document.body.classList.contains('sidebar-open')) {
        toggleSidebar(false); //       
      }
    });
  }

  setupCloseButton(toggleSidebar) {
    const closeButton = document.querySelector('.close-menu'); //     
    if (closeButton) {
      closeButton.addEventListener('click', () => {
        console.log('Close button clicked'); //     
        toggleSidebar(false); //       
      });
    }
  }

  //   
  createOverlay() {
    const overlay = document.createElement('div'); //   div 
    overlay.id = 'sidebar-overlay'; //   
    overlay.className = 'fixed inset-0 bg-black bg-opacity-50 z-40 transition-opacity duration-300 opacity-0 invisible'; //   
    return overlay; //  
  }

  //       
  initiateSidebarSubmenus(sidebar) {
    const subMenuTriggers = sidebar.querySelectorAll('.menu-item.has-children'); //     
    
    subMenuTriggers.forEach(trigger => {
      const subMenu = trigger.querySelector('.sub-menu'); //    
      if (!subMenu) return; //       

      //   
      const toggleBtn = document.createElement('button'); //   
      toggleBtn.className = 'submenu-toggle w-8 h-8 flex items-center justify-center'; //   
      toggleBtn.innerHTML = '<i class="sicon-keyboard_arrow_down text-sm transition-transform duration-200"></i>'; //   
      trigger.appendChild(toggleBtn); //     

      toggleBtn.addEventListener('click', (e) => {
        e.preventDefault(); //   
        e.stopPropagation(); //       

        const isOpen = subMenu.classList.contains('is-open'); //       
        
        //   
        if (isOpen) {
          this.anime(subMenu, {
            height: [subMenu.scrollHeight, 0], //    
            duration: 300, //   
            easing: 'easeOutQuad', //  
          }).then(() => {
            subMenu.classList.remove('is-open'); //   
            toggleBtn.querySelector('i').classList.remove('rotate-180'); //    
          });
        } else {
          subMenu.classList.add('is-open'); //   
          toggleBtn.querySelector('i').classList.add('rotate-180'); //  
          this.anime(subMenu, {
            height: [0, subMenu.scrollHeight], //    
            duration: 300, //   
            easing: 'easeOutQuad', //  
          });
        }
      });

      //     0
      subMenu.style.height = '0'; //      0
      subMenu.style.overflow = 'hidden'; //   
    });
  }

  //   ...
  commonThings() {
    this.cleanContentArticles('.content-entry'); //   
  }

  cleanContentArticles(elementsSelector) {
    let articleElements = document.querySelectorAll(elementsSelector); //    
    if (articleElements.length) {
      articleElements.forEach(article => {
        article.innerHTML = article.innerHTML.replace(/\&nbsp;/g, ' '); //   
      });
    }
  }

  // ... (   )

  //       
  initAddToCart() {
    salla.cart.event.onUpdated(summary => {
      //     
      document.querySelectorAll('[data-cart-total]').forEach(el => el.innerText = salla.money(summary.total));
      document.querySelectorAll('[data-cart-count]').forEach(el => el.innerText = salla.helpers.number(summary.count));
    });

    salla.cart.event.onItemAdded((response, prodId) => {
      const cartIcon = document.querySelector('salla-cart-summary'); //    
      const productImage = document.querySelector(`#product-${prodId} img`); //    
      if (cartIcon && productImage) {
        cartIcon.animateToCart(productImage); //        
      }
    });
  }

  initThemeToggle() {
    const themeButton = document.getElementById('theme-button');
    themeButton.addEventListener('click', () => {
      toggleDarkMode(); //    dark-mode.js
    });
  }
}

//  
salla.onReady(() => {
  const appInstance = new App();
  appInstance.loadTheApp();
  appInstance.initThemeToggle(); //    
});

